<template>
  <div class="row">
    <div class="col-12">

      <div id="product-content" class="pr-4 pl-5 pt-5 pb-5">

        <div id="product-info" class="mb-6">
          <p class="font-weight-bold fs-18 mb-2">{{ product.name|capitalizeLetters }}</p>

          <p class="mb-2">
            <span class="mr-1"><del>{{ $n(200, 'currency') }}</del></span>
            <span>{{ $n(product.unit_price, 'currency', $i18n.locale) }}</span>
          </p>

          <div class="d-flex justify-content-left">
            <div class="mr-3">
              <font-awesome-icon icon="star" />
              <font-awesome-icon icon="star" />
              <font-awesome-icon icon="star" />
              <font-awesome-icon icon="star" />
            </div>
            <b-link @click="goToReviews">{{ numberOfReviews }} {{ $tc('review', numberOfReviews) }}</b-link>
          </div>
        </div>
        
        <!-- TODO: Put triggers for these sections -->
        <!-- <p id="multiple-payments">
          <font-awesome-icon icon="info-circle" class="mr-1" />
          {{ $t('interest_rate_payments', { value: $n(calculatePaymentFraction(product.unit_price), 'currency') }) }}
        </p>

        <p>
          Livr en un jour et <b-link v-b-popover.hover.bottom="returnPolicy" class="font-weight-bold">retour GRATUITS</b-link>
        </p> -->

        <!-- <div v-if="hasDescription" id="description">
          <p class="lead font-weight-bold">Description</p>
          <p>
            Lorem ipsum dolor sit amet consectetur adipisicing elit. Et dolor suscipit libero 
            eos atque quia ipsa sint voluptatibus! Beatae sit assumenda asperiores iure at maxime
            atque repellendus maiores quia sapiente.
          </p>
        </div> -->

        <!-- Actions -->
        <actions class="my-7" :product="product" :colors="colors" :sizes="sizes" />

        <div class="my-4">
          <b-link v-b-popover.click.bottom="securedTransactionText">
            <font-awesome-icon icon="lock" class="mr-2" />
            {{ $t('secured_payments') }}
          </b-link>
        </div>

        <!-- Optional - Size guide -->
        <size-guide />

        <!-- Information -->
        <additional-information :product="product" />
  <!-- 
        <b-form-checkbox v-model="cartOptions.is_gift">
          Ceci sera un cadeau
        </b-form-checkbox> -->

        <v-menu v-if="isAuthenticated" :close-on-content-click="false" transition="scale-transition">
          <template v-slot:activator="{ on, attrs }">
            <v-btn class="my-3" v-bind="attrs" v-on="on" @click="getUserLists">
              <font-awesome-icon class="mr-2" icon="star"></font-awesome-icon>
              {{ $t('Add to list') }}
            </v-btn>
          </template>

          <v-list>
            <v-list-item-group>
              <v-list-item v-for="item in wishlists" :key="item.id" @click="addToList(item)">
                {{ item.name }}
              </v-list-item>
            </v-list-item-group>
          </v-list>
        </v-menu>

        <b-card class="shadow-none border my-4">
          <b-card-text>
            <b-container>
              <v-row>
                <v-col cols="12">
                  <v-row>
                    <v-col cols="1"><v-icon class="mr-4">mdi-truck-delivery</v-icon></v-col>
                    <v-col cols="10">{{ $t('delivery_conditions', { value: $n(50, 'currency') }) }}</v-col>
                  </v-row>
                </v-col>

                <v-col cols="12">
                  <v-row>
                    <v-col cols="1"><v-icon class="mr-4">mdi-clock-time-nine</v-icon></v-col>
                    <v-col cols="10">{{ $t('express_delivery_fee', { value: $n(50, 'currency'), fee: $n(10, 'currency') }) }}</v-col>
                  </v-row>
                </v-col>
              </v-row>
            </b-container>
          </b-card-text>
        </b-card>

        <!-- <div class="bg-light p-3">
          <p class="font-weight-bold">Le saviez-vous ?</p>
          <span>
            La musique HD ncessite un service de musique en streaming compatible, 
            comme Amazon Music Unlimited.
          </span>
        </div> -->

        <div class="bg-light p-3 rounded">
          Nos prix incluent l'co-participation sur tous les 
          produits concerns. Vous voulez recycler votre appareil 
          lectrique ou lectronique gratuitement ? <b-link>En apprendre plus</b-link>
        </div>

      </div>
    
    </div>
  </div>
</template>

<script>
import { mapGetters, mapState } from 'vuex'

import SizeGuide from "./information/SizeGuide.vue"
import Actions from './information/Actions.vue'
import AdditionalInformation from './information/AdditionalInformation.vue'

export default {
  name: 'Information',
  components: { SizeGuide, Actions, AdditionalInformation },
  props: {
    product: {
      type: Object,
      required: true
    },
    numberOfReviews: {
      type: Number,
      default: 0
    },
    colors: {
      type: Array,
      default: () => []
    },
    sizes: {
      type: Array,
      default: () => []
    }
  },

  data: () => ({
    returnPolicy: "Les retours gratuits sont disponibles pour l adresse d expdition que vous avez choisie. Vous pouvez retourner l article pour n importe quelle raison dans son tat neuf et inutilis, sans frais de retour.",
    securedTransactionText: "Nous nous efforons de protger votre scurit et votre vie prive. Notre systme de paiement scuris chiffre vos donnes lors de la transmission. Nous ne partageons pas les dtails de votre carte de crdit avec les vendeurs tiers, et nous ne vendons pas vos donnes personnelles  autrui. En savoir plus"
  }),

  computed: {
    ...mapState({
      wishlists: (state) => { return state.shopModule.userLists }
    }),
    ...mapGetters('authenticationModule', ['isAuthenticated']),

    hasDescription() {
      return this.product == undefined
    }
  },
  
  methods: {
    goToReviews () {
      // Scroll down to the reviews section
      // of the product page
      document.getElementById('reviews').scrollIntoView()
    },

    calculatePaymentFraction (unitPrice) {
      var price = unitPrice * 1
      return Math.floor(price / 4 * 1000) / 1000
    },
    
    addToList(wishlist) {
      if (!this.isAuthenticated) {
        this.$store.commit('authenticationModule/loginUser')
      } else {
        this.$api.shop.lists.add(this.product, wishlist)
        .then((response) => {
          response
        })
        .catch((error) => {
          error
        })
      }
    },

    getUserLists() {
      this.$api.shop.lists.all()
      .then((response) => {
        this.$store.commit('setUserLists', response.data)
      })
      .catch((error) => {
        error
      })
    }
  }
}
</script>

