<template>
  <div id="actions">
    <p class="font-weight-bold m-0">
      {{ $t('Size') }}
    </p>

    <div v-if="sizes.length > 0" id="sizes" class="my-3">
      <b-btn v-for="size in sizes" :key="size.key" variant="light">
        {{ size.name }}
      </b-btn>
    </div>

    <div v-else id="sizes" class="my-3">
      <b-btn disabled>
        {{ $t('Unique size') }}
      </b-btn>
    </div>

    <div class="d-flex justify-content-left">
      <!-- Quantity -->
      <b-form-input v-model="cartOptions.quantity" :min="1" :max="99" :step="1" type="number" aria-label="Quantity" style="width: 100px"></b-form-input>
      
      <!-- Add to cart -->
      <b-btn class="btn-md my-0 mx-2" variant="primary" @click="addToCart">
        <font-awesome-icon icon="shopping-cart"></font-awesome-icon>
        {{ $t('Add to cart') }}
      </b-btn>

      <!-- Add to like -->
      <b-btn class="btn-md my-0" variant="danger" @click="addToLikes">
        <font-awesome-icon icon="heart" />
      </b-btn>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'Actions',

  props: {
    product: {
      type: Object
    },
    quantity: {
      type: Number,
      default: 0
    },
    colors: {
      type: Array,
      default: () => []
    },
    sizes: {
      type: Array,
      default: () => []
    }
  },

  data: () => ({
    cartOptions: {
      quantity: 1,
      color: null,
      is_gift: false
    }
  }),


  computed:{
    ...mapGetters('authenticationModule', ['isAuthenticated'])
  },

  methods: {
    addToCart () {
      // Add product to cart
      this.$api.shop.cart.add(this.product, this.cartOptions)
      .then((response) => {
        this.$store.commit('updateCart', response.data)
      })
      .catch((error) => {
        this.$store.dispatch('addErrorMessage', error.error)
      })
    },

    addToLikes () {
      // Add product to liked products
      if (!this.isAuthenticated) {
        this.$store.commit('authenticationModule/loginUser')
      } else {
        this.$api.shop.lists.like(this.product)
        .then((response) => {
          response
        })
        .catch((error) => {
          error
        })
      }
    }
  }
}
</script>

<style scoped>
  #sizes .btn {
    border-radius: 10rem;
  }
</style>
