<template>
  <div class="d-flex flex-direction-colum justify-content-around">

    <!-- Images -->
    <div id="images" style="width: 15%;margin-right: .5rem;">
      <b-img v-for="(image, index) in images" :key="image.id" :alt="image.name" :src="image.mid_size|mediaUrl" class="mb-2 hover-shadow" fluid @click="changeImage(index)" />
          
      <b-btn v-if="hasVideo" variant="light" block @click="playVideo = !playVideo">
        <font-awesome-icon icon="play" />
      </b-btn>

      <b-btn variant="light" block>
        <font-awesome-icon icon="share" />
      </b-btn>
    </div>

    <div id="image">
      <!-- Video -->
      <b-embed v-if="playVideo & hasVideo" :poster="mainImage.original|mediaUrl" type="video" aspect="4by3" controls>
        <source :src="productVideo.content|mediaUrl" type="video/mp4">
      </b-embed>
      
      <!-- Image -->
      <b-img v-else :src="displayImage.mid_size|mediaUrl" :alt="displayImage.name" fluid />
    </div>
  </div>
</template>

<script>
var _ = require('lodash')

export default {
  name: 'Images',
  props: {
    images: {
      type: Array,
      required: true
    },
    productVideo: {
      type: Object,
      default: () => {}
    }
  },
  data: () => ({
    playVideo: false,
    cursor: 0
  }),
  computed: {
    mainImage () {
      // Returns the product's main image
      var mainImage = _.find(this.images, ['is_main_image', true])
      return _.isUndefined(mainImage) ? this.images[0] : mainImage
    },
    displayImage () {
      return this.images[this.cursor]
    },
    hasVideo () {
      return !_.isUndefined(this.productVideo) || !_.isNull(this.productVideo)
    }
  },
  methods: {
    changeImage (index) {
      if (this.playVideo) { this.playVideo = false }
      this.cursor = index
    }
  }
}
</script>

<style scoped>
  #images img {
    cursor: pointer;
  }
  .embed-responsive video {
    width: 100%;
    height: auto;
  }
</style>
