<template>
  <section v-if="isLoading">
    <div class="container">
      <div class="row">
        <div class="col-md-12 d-flex justify-content-left">
          <b-skeleton width="10%" />
          <b-skeleton width="10%" class="ml-2" />
          <b-skeleton width="10%" class="ml-2" />
        </div>
        
        <div class="col-md-7">

          <div class="row">
            <div class="col-12">
              
              <div class="row">
                <div class="col-2">
                  <b-skeleton-img v-for="i in 3" :key="i" height="80px" class="mb-2" />
                </div>

                <div class="col-10">
                  <b-skeleton-img height="600px" />
                </div>
              </div>
          </div>

          </div>
        </div>

        <div class="col-md-5">
          <b-skeleton width="100%" class="mt-3" />
          <b-skeleton width="50%" class="mt-3" />
          <b-skeleton width="30%" class="mt-3" />

          <div id="actions" class="d-flex justify-content-left my-5">
            <b-skeleton width="30%" />
            <b-skeleton width="30%" class="mx-2" />
            <b-skeleton width="30%" />
          </div>
          
          <b-skeleton width="100%" height="200px" class="mt-3" />
          <b-skeleton width="100%" height="200px" class="mt-3" />
        </div>
      </div>
    </div>
  </section>

  <section v-else id="product" class="space">
    <div class="container dark-grey-text mt-5">

      <div class="row">
        <!-- <div class="col-12">
          <b-breadcrumb :items="breadcrumbs" class="pl-1"></b-breadcrumb>
        </div> -->

        <div class="col-md-7">
          <!-- Images -->
          <images :images="images" :product-video="currentProduct.video" />
        </div>

        <div class="col-md-5">
          <information :product="currentProduct" :number-of-reviews="numberOfReviews" />
        </div>
      </div>

      <hr>

      <!-- Additional information -->
      <more-products :colors="colors" :sizes="sizes" />

      <div class="row d-none">

        <!-- More -->
        <div class="col-12">
          <div class="row">
            <div id="more" class="p-1">
              <div class="d-flex justify-content-around p-4">
                <b-img v-for="i in 10" :key="i" src="http://via.placeholder.com/200x300" class="mr-2" fluid />
              </div>
            </div>            
          </div>
        </div>

        <hr>

        <!-- Recently viewed -->
        <div class="col-12">
          <recently-viewed />
        </div>

      </div>

      <hr>
      
      <!-- Reviews -->
      <reviews :reviews="currentProductReviews" />
    </div>

  </section>
</template>

<script>
var _ = require('lodash')

import MoreProducts from '../components/product/MoreProducts.vue'
import Images from '../components/product/Images.vue'
import Information from '../components/product/Information.vue'
import Reviews from '../components/product/Reviews.vue'
import RecentlyViewed from '../components/product/RecentlyViewed.vue'
import { mapMutations } from 'vuex'

export default {
  name: 'Product',

  components: {
    Images,
    Information,
    Reviews,
    MoreProducts,
    RecentlyViewed
  },
  
  title: vm => `Product name ${vm}`,

  data: () => ({
    isLoading: true
  }),
  
  computed: {
    currentProduct () {
      return this.$store.state.shopModule.currentProduct
    },

    currentProductReviews () {
      return this.$store.state.shopModule.currentProductReviews
    },
    
    images () {
      // Returns all the images for the
      // current product
      try {
        return this.currentProduct.images
      } catch (error) {
        return []
      }
    },
    
    numberOfReviews () {
      return this.currentProductReviews.length
    },

    breadcrumbs() {
      return [
          {
            text: 'Home',
            to: 'home'
          },
          {
            text: 'Collections',
            to: 'collection_all'
          },
          {
            text: this.currentProduct.name,
            active: false
          }
        ]
    },

    colors() {
      var items = _.filter(this.currentProduct.variants, { category: 'Color' })
      return _.uniqBy(items, 'name')
    },

    sizes() {
      var items = _.filter(this.currentProduct.variants, { category: 'Size' })
      return _.uniqBy(items, 'name')
    }
  },

  watch: {
    $route(n, o) {
      if (n.params.id != o.params.id) {
        this.$store.commit('setCurrentProduct', n.params.id)
      }
    }
  },

  created() {
    if (!this.$store.getters['hasProducts']) {
      this.$store.commit('setProducts', this.$session.get('products'))
    }
    var product = this.$store.getters['getProduct'](this.$route.params.id)
    this.$store.commit('setCurrentProduct', product)
  },

  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.$store.commit('setRecentlyViewed', to.params.id)
      vm.$session.set('recentlyViewedProducts', vm.$store.state.shopModule.recentlyViewed)
    })
  },

  mounted() {
    setTimeout(() => {
      this.isLoading = false
    }, 500);
  },
  
  methods: {
    ...mapMutations(['addToRecentlyViewed'])
  }
}
</script>

<style scoped>
  #more div {
    height: 500px;
    width: 100%;
    /* background-color: blue; */
    overflow-x: scroll;
    padding: 1rem;
  }

  section#product {
    height: auto;
    min-height: 600px;
  }
</style>
